.section-users-questions .form-in-text {
  margin-bottom: 20px;
}
.section-users-questions hr.dashed {
  margin-top: 30px;
  margin-bottom: 30px;
}
@media (max-width: 500px) {
  .section-users-questions hr.dashed {
    margin-top: 20px;
    margin-bottom: 20px;
  }
}
.section-users-individuals-connection-status .grid_6-cells {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-rows: 1fr 1fr;
  grid-gap: 15px;
}
.section-users-individuals-connection-status .grid_6-cells > * {
  align-self: center;
}
.section-users-individuals-connection-status .grid_6-cells .btn {
  justify-self: center;
}
@media (max-width: 768px) {
  .section-users-individuals-connection-status .grid_6-cells {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    grid-gap: 15px;
  }
  .section-users-individuals-connection-status .grid_6-cells .btn {
    justify-self: start;
  }
}
.section-users-switch-off .form-footer {
  margin-top: 20px;
}
.viv-calc {
  display: grid;
  grid-template-areas: 'left vertical-divider right' 'bottom bottom bottom';
  grid-template-columns: 1fr auto 1fr;
  column-gap: 55px;
  row-gap: 20px;
  margin-top: 50px;
}
.viv-calc__left {
  grid-area: left;
}
.viv-calc__right {
  grid-area: right;
}
.viv-calc__bottom {
  grid-area: bottom;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.viv-calc__bottom-line {
  position: absolute;
  top: 52%;
  transform: translateY(-48%);
  width: 100%;
  height: 2px;
  border-top: 1px solid #1074b9;
}
.viv-calc__vertical-divider {
  grid-area: vertical-divider;
  height: 100%;
  width: 2px;
  border-left: 1px dashed #1074b9;
}
.viv-calc__checkbox-row {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
}
.viv-calc__checkbox-prepend-text {
  margin-right: 10px;
}
.viv-calc .input-with-label:not(:last-child) {
  margin-bottom: 27px;
}
.viv-calc label {
  font-size: 1.07142857rem;
}
@media (max-width: 768px) {
  .viv-calc {
    display: grid;
    grid-template-areas: 'left' 'vertical-divider' 'right' 'bottom';
    grid-template-columns: auto;
    margin-top: 20px;
  }
  .viv-calc__vertical-divider {
    height: 2px;
    width: 100%;
    border-top: 1px dashed #1074b9;
    border-left: none;
  }
  .viv-calc__checkbox-row {
    margin-bottom: 15px;
  }
  .viv-calc .input-with-label:not(:last-child) {
    margin-bottom: 20px;
  }
}
.section-users-connection-calc .grid-icons {
  margin-top: 70px;
}
@media (max-width: 1024px) {
  .section-users-connection-calc .grid-icons {
    margin-top: 60px;
  }
}
@media (max-width: 850px) {
  .section-users-connection-calc .grid-icons {
    margin-top: 50px;
  }
}
@media (max-width: 768px) {
  .section-users-connection-calc .grid-icons {
    margin-top: 40px;
  }
}
@media (max-width: 500px) {
  .section-users-connection-calc .grid-icons {
    margin-top: 30px;
  }
}
